import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Toast, ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { Util } from 'src/app/commons/util/util';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { UtilValidation } from 'src/app/commons/util/util.validation';
import { autenticar, setAuthenticado } from 'src/app/store/actions/auth.actions';
import { AppState } from 'src/app/store/app.reducer';

@Component({
  selector: 'app-login-form',
  templateUrl: './login.component.html',
  styles: [
  ]
})
export class LoginComponent implements OnInit, OnDestroy {

  public formularioLogin: FormGroup;
  public loading = false;
  public user = "Rut Académico";
  private _subscription: Subscription;

  constructor(
    private appService: AppService,
    private store: Store<AppState>,
    private authenticationService: AuthenticationService,
    private router: Router,
    private utilValidation: UtilValidation,
    private toastr: ToastrService,
  ) {
    this.appService.pageTitle = 'Login';
  }
  ngOnDestroy() {
    this._subscription.unsubscribe();
  }


  ngOnInit() {
    this.formularioLogin = new FormGroup({
      tipo: new FormControl('', [Validators.required]),
      username: new FormControl('', [Validators.required]),
      password: new FormControl('', Validators.required)
    });

    this.formularioLogin.controls.tipo.setValue('docente');
    this.changeTipo();

    this._subscription = this.store.select('auth').subscribe(state => {
      this.loading = state.loading;

      if (state.authenticate && !state.logout) {
        this.router.navigate(['/home']);
      }

    });

    this.authenticationService.isLogin().then(
      (loEsta) => {
        if (loEsta) {
          this.store.dispatch(setAuthenticado({ usuario: this.authenticationService.obtenerUserName() }));
        }
      }
    );

  }

  changeTipo() {
    var tipo = this.formularioLogin.controls.tipo.value;
    if (tipo === 'docente') {
      this.formularioLogin.controls.username.setValidators([this.utilValidation.rutValido, Validators.required]);
      this.formularioLogin.controls.username.updateValueAndValidity();
      this.user = "Rut Académico"
    } else {
      this.formularioLogin.controls.username.clearValidators();
      this.formularioLogin.controls.username.setValidators([Validators.required]);
      this.formularioLogin.controls.username.updateValueAndValidity();
      this.user = "Usuario de correo sin @uft.cl"
    }

  }

  formatearRut() {
    if (this.formularioLogin.controls.tipo.value === 'docente') {
      let valorPreFormato = this.formularioLogin.controls.username.value;
      let rutFormateado = Util.getRutFromRutString(valorPreFormato) + Util.getDvFromRutString(valorPreFormato);
      if (valorPreFormato.startsWith('0') || rutFormateado.startsWith('0')) rutFormateado = valorPreFormato;
      this.formularioLogin.controls.username.setValue(rutFormateado);
    }
  }

  login() {
    console.log(this.formularioLogin)
    UtilValidation.setFormForValidate(this.formularioLogin);
    if (this.formularioLogin.valid && !this.loading) {
      this.loading = true;
      const usuario = this.formularioLogin.controls.username.value;
      const password = this.formularioLogin.controls.password.value;
      let tipo = '';
      /* this.store.dispatch(autenticar({ usuario: { usuario: usuario, clave: password } })); */
      if (this.formularioLogin.controls.tipo.value === 'docente') {
        this.formatearRut();
        tipo = 'd'
      }

      const login = {
        usuario: usuario,
        password: password,
        tipo: tipo
      };
      this.store.dispatch(autenticar({ login }));
    }
    else {
      this.toastr.warning("Verifique que está ingresando un usuario y/o contraseña válido");
    }
  }

}
